import React, { useState, useEffect } from 'react'
import numberService from './services/numbers';
import './app.css'

const Person = ({ person }) => {
  const deletePeson = (id) => {
    if (window.confirm(`Do you want to delete ${person.name}`)) {
      numberService
        .deletePerson(id)
    }
  }

  return <p>{person.name} {person.number} <button onClick={()=>deletePeson(person.id)}>delete</button></p>  
}

const Persons = ({ persons, filter }) => {
  const newPersons = persons.filter(person => person.name.toUpperCase().startsWith(filter.toUpperCase()))
  return (
    <>
      {newPersons.map(person => {
        return <Person key={person.name} person={person} />    
      }
      )}
    </>
  )
}

const PeronsForm = ({ addPerson, newName, newNumber, handleNameChange, handleNumberChange }) => {
  return (
    <form onSubmit={addPerson}>
      <div>
        name: <input value={newName} onChange={handleNameChange} />
      </div>
      <div>
        number: <input value={newNumber} onChange={handleNumberChange} />
      </div>
      <div>
        <button type="submit">add</button>
      </div>
      <div>debug: {newName}</div>
    </form>
  )
}

const Filter = ({ filter, handleFilterChange }) => {
  return (
    <div>
      filter shown with: <input value={filter} onChange={handleFilterChange} />
    </div>
  )
}


const App = () => {
  const [ persons, setPersons ] = useState([])
  const [ newName, setNewName ] = useState('')
  const [ newNumber, setNewNumber ] = useState('')
  const [ filter, setFilter ] = useState('')
  const [ errorMsg, setErrorMsg ] = useState('')

  useEffect(() => {
    setTimeout(() => {
      numberService
      .getAll()
      .then(response => {
        setPersons(response.data)
      })
    }, 500)
  }, [persons])

  const handleNameChange = (event) => {
    setNewName(event.target.value)
  }

  // Add a new person and phone number
  const addPerson = (event) => {
    event.preventDefault()
    
    const newPerson = {
      name: newName,
      number: newNumber
    }

    let exists = -1
    persons.forEach(person => {
      if (person.name === newName)
        exists = person.id
    })

    if (exists !== -1) {
      numberService
        .update(exists, newPerson)
        .then(returnedPerson => {
          setPersons(persons.map(person => person.id === exists ? returnedPerson.data : person))
        })
        .catch(error => {
          setErrorMsg(
            `Note '${newPerson.newName}' was already removed from server`
          )
          setTimeout(() => {
            setErrorMsg(null)
          }, 5000)
          setPersons(persons.filter(n => n.id !== newPerson.newNumber))
        })

    } else {
      numberService
        .create(newPerson)
        .then(returnedPerson => {
          setPersons(persons.concat(returnedPerson.data))
        })
      setTimeout(() => {
        setErrorMsg(``)
      }, 5000)
      setErrorMsg(`Added ${newName}`)
      setNewName('')
      setNewNumber('')
    }
  }

  const handleNumberChange = (event) => {
    setNewNumber(event.target.value)
  }

  const handleFilterChange = (event) => {
    setFilter(event.target.value)
  }

  return (
    <div>
      <h2>Phonebook</h2>
        <Filter filter={filter} handleFilterChange={handleFilterChange} />
      <h2>add a new</h2>
        {(errorMsg !== '') && <p className="success">{errorMsg}</p>}  
        <PeronsForm addPerson={addPerson} newName={newName} newNumber={newNumber} handleNameChange={handleNameChange} handleNumberChange={handleNumberChange} />
      <h2>Numbers</h2>
        <Persons persons={persons} filter={filter} />
    </div>
  )
}

export default App